body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.highlight abbr {
  color: #54b877;
  background: #edf5f0;
  border-radius: 50%;
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
}

.background_split {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(
    #54b877 0%,
    #54b877 50%,
    #edf5f0 50%,
    #edf5f0 100%
  );
}

.header {
  width: 80vw;
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.title1 {
  font-size: small;
  color: white;
  margin: 0px;
  margin-top: 30px;
}

.title2 {
  font-size: x-large;
  color: white;
  font-weight: bold;
  margin: 20px 0px;
}

.wrapper {
  width: 80vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  background-color: white;
  margin-bottom: 40px;
}

.content_top {
  width: 60vw;
  display: flex;
  justify-content: space-between;
}

.date {
  font-size: large;
  color: #54b877;
  font-weight: bold;
  margin: 20px 0px;
}

.content_top_left {
  width: 7vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.line_horizontal {
  width: 100%;
  border: 0;
  height: 1px;
  margin: 0;
  background: #cbead6;
}

.content_bottom {
  width: 60vw;
  display: flex;
  justify-content: space-between;
  margin: 12vh 0px;
}

.content_bottom_right {
  width: 35vw;
  display: flex;
  flex-direction: column;
}

.content_bottom_right_top {
  height: 4vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content_bottom_right_bottom {
  height: 16vw;
  display: flex;
  padding: 20px 0px;
  box-sizing: border-box;
  border-top: 1.5px solid #cbead6;
  border-bottom: 1.5px solid #cbead6;
  border-right: 0px;
  border-left: 0px;
}

.temperature {
  display: flex;
}

.temperature_value {
  font-size: xx-large;
  color: #54b877;
  font-weight: 500;
  margin: 0px;
}

.temperature_sign {
  font-size: small;
  color: #54b877;
  margin: 0px;
}

.line_vertical {
  border: 0;
  border-left: 1px solid #cbead6;
  height: 70%;
  width: 0.5px;
  margin: 0;
  background: #cbead6;
}

.temperature_info {
  font-size: small;
  color: #54b877;
  font-weight: bold;
  margin: 0px;
}
